import React, {useState} from 'react'
import { useStaticQuery, graphql,Link } from 'gatsby'

import GoogleMapReact from 'google-map-react'
import PropertyItem from '../../../../Global/Pages/PropertyList.component'
import Pagination from '../../../../Global/Pages/Pagination.component'
import AdvanceSearchWidgetSidebar from '../../../../Global/App/SidebarWidgets/AdvancedSearchWidget.component'
import { HalfMapSearchResult } from '../../../../Global/Pages/Listing/HalfMap/HalfMapSearchResult.component'

const HalfMapV2Section=()=>{
    const [SideBarShow, setSideBarShow] = useState(false);
    const [SideBarMainShow, setSideBarMainShow] = useState(false);
    const handleMainSidebar=()=>{
        setSideBarMainShow(!SideBarMainShow)
        console.log(SideBarMainShow)
        document.querySelector('#mainsidebar').classList.toggle('is-full-width')
    }
    const handleSidebar=()=>{
        setSideBarShow(!SideBarShow)
            document.querySelector('body').classList.toggle('body_overlay')
            document.querySelector('#mobilesidebar').classList.toggle('sidebar_ml0')
    }
    const data = useStaticQuery(graphql`
        query{
            fp1: file(relativePath: {eq: "property/fp1.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
            fp2: file(relativePath: {eq: "property/fp2.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp3: file(relativePath: {eq: "property/fp3.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
            fp4: file(relativePath: {eq: "property/fp4.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp5: file(relativePath: {eq: "property/fp5.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp15: file(relativePath: {eq: "property/fp15.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp16: file(relativePath: {eq: "property/fp16.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp17: file(relativePath: {eq: "property/fp17.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp18: file(relativePath: {eq: "property/fp18.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp19: file(relativePath: {eq: "property/fp19.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp20: file(relativePath: {eq: "property/fp20.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
        }`)
   
    return(
        <section id="feature-property" className="our-listing bgc-f7 pt0 pb0">
		<div className="container-fluid">
                <div className="row">
                    <div className="col-lg-12">
                        <div className="listing_sidebar dn db-991">
                            <div id="mobilesidebar"className="sidebar_content_details style3 ">
                                <div className="sidebar_listing_list style2 mobile_sytle_sidebar mb0">
                                    <div className="sidebar_advanced_search_widget">
                                        <h4 className="mb25">
                                        Advanced Search <Link className="filter_closed_btn float-right" to="#" onClick={handleSidebar}><small>Hide Filter</small> <span className="flaticon-close"></span>
                                        </Link>
                                        </h4>
                                        <AdvanceSearchWidgetSidebar index="1" style2/>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row">
				<div className="col-sm-12">
					<div className="listing_sidebar dn-991">
						<div id="mainsidebar" className="sidebar_content_details is-full-width">
							<div className="sidebar_listing_list style2 mb0">
								<div className="sidebar_advanced_search_widget">
									<h4 className="mb25">Advanced Search</h4>
                                    <AdvanceSearchWidgetSidebar index="2" style2/>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-lg-12">
                        <div className="sidebar_switch mobile_style dn db-991 mt30 mt0-767 mb0">
                            <div id="main">
                                <span id="open" className="flaticon-filter-results-button filter_open_btn" onClick={handleSidebar}> Show Filter</span>
                            </div>
                        </div>
                    </div>
                    <div className="col-xl-5">
                        <div className="half_map_area_content mt30">
                            <div className="row">
                                <HalfMapSearchResult more/>
                            </div>
                            <div className="row">
                                <div className="col-lg-12">
                                    <ul className="feature_property_half_clist style2 mb0">
                                        <li className="extrawide list-inline-item">
                                        <PropertyItem listfor='For Rent'
                                        featured
                                        price="13000"
                                        category="Appartment"
                                        title="Renovated Apartment"
                                        >
                                            <div className="item">
                                                <img className="img-whp" src={data.fp1.childImageSharp.fluid.originalImg} alt="fp1.jpg"/>
                                            </div>
                                            <div className="item">
                                                <img className="img-whp" src={data.fp2.childImageSharp.fluid.originalImg}alt="fp2.jpg"/>
                                            </div>
                                        </PropertyItem>
                                    </li>
                                        <li className="extrawide list-inline-item">
                                            <PropertyItem listfor='For Rent'
                                                price="13000"category="Villa"
                                                title="Gorgeous Villa Bay View"
                                                >
                                                    <div className="item">
                                                        <img className="img-whp" src={data.fp2.childImageSharp.fluid.originalImg} alt="fp1.jpg"/>
                                                    </div>
                                                    <div className="item">
                                                        <img className="img-whp" src={data.fp3.childImageSharp.fluid.originalImg}alt="fp2.jpg"/>
                                                    </div>
                                                </PropertyItem>
                                            </li>
                                        <li className="extrawide list-inline-item">
                                                <PropertyItem listfor='For Sale'
                                                    price="13000"category="Single Family Home"
                                                    title="Luxury Family Home"
                                                    >
                                                        <div className="item">
                                                            <img className="img-whp" src={data.fp3.childImageSharp.fluid.originalImg} alt="fp3.jpg"/>
                                                        </div>
                                                        <div className="item">
                                                            <img className="img-whp" src={data.fp4.childImageSharp.fluid.originalImg}alt="fp4.jpg"/>
                                                        </div>
                                                </PropertyItem>
                                            </li>
                                        <li className="extrawide list-inline-item">
                                        <PropertyItem listfor='For Rent'
                                                price="13000"category="Apartment"
                                                title="Luxury Family Home"
                                                featured
                                                >
                                                    <div className="item">
                                                        <img className="img-whp" src={data.fp4.childImageSharp.fluid.originalImg} alt="fp4.jpg"/>
                                                    </div>
                                                    <div className="item">
                                                        <img className="img-whp" src={data.fp5.childImageSharp.fluid.originalImg}alt="fp5.jpg"/>
                                                    </div>
                                        </PropertyItem>
                                        </li>
                                    </ul>
                                </div>
                                <div className="col-lg-12 mb30">
                                    <Pagination/>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-xl-7">
                        <div className="sidebar_switch style2 text-right dn-991">
                            <div id="main3">
                                {!SideBarMainShow ? 
                                <span id="open3" class="filteropen2 showBtns flaticon-filter-results-button sidebarClosed2" onClick={handleMainSidebar}>Show Filter</span>
                                 
                                    :
                                <span id="open3" class="filteropen2 showBtns flaticon-close sidebarOpended2" onClick={handleMainSidebar}>Hide Filter</span>
                                    
                                }
                            </div>
                        </div>
                        <div className="half_map_area">
                            <div className="home_two_map style2">
                                <div className="map-canvas skin2 half_style" id="contact-google-map" data-map-lat="51.510280" data-map-lng="-0.084028" data-icon-path="images/logo/1.png" data-map-title="Awesome Place" data-map-zoom="14">
                                    <GoogleMapReact
                            bootstrapURLKeys={{ key: 'AIzaSyAAz77U5XQuEME6TpftaMdX0bBelQxXRlM' }}
                            defaultCenter={{
                                            lat: 51.510280,
                                            lng: -0.084028
                                            }}
                            defaultZoom={14}
                            >
                                <div
                                    >
                                        Awesome Place
                                </div>
                        </GoogleMapReact>
                                </div>
                            </div>
					</div>
				</div>
			</div>
		</div>
	</section>
    )

}
export default HalfMapV2Section